.chat-panel{
    background: #222224;
    width: 210px;
    height: 100%;

    padding: 0 20px 0 20px;
    box-shadow: 1px 0 5px 0px rgba(0, 0, 0, 0.1);


    -webkit-user-select: none;


       -moz-user-select: none;


        -ms-user-select: none;


