}

.chat-list {
    padding: 20px 0 10px 0;

}

.member-list {
    padding: 10px 0 20px 0;
}

.chat-list-title, .member-list-title {
    font-weight: 600;
    font-size: 20px;
    padding-bottom: 5px;
    border-bottom: 1px solid #525252;

}

.chat-list-items, .member-list-items {
    padding: 10px 0 10px 0;
}

.chat-list-item, .member-list-item {
    padding: 5px 10px 7px 10px;
