    background: #2d2d35;
}

.member-list-item strong, .chat-list-item strong {
    font-family: 'Ubuntu', sans-serif;
    font-weight: 300;
    
    display: inline-block;
    margin-right: 5px;

    vertical-align: middle;
}

.member-list-item strong {
    color: #45a199;
    margin-top: -3px;
}

